#gf-plugin-customizer {

    .components-placeholder {
        background: #f1f1f1;
    }

    .gf-block__main {
        margin-left: auto;
        margin-right: auto;

        .components-panel {
            background: none;
            border: none;
        }

        .components-panel__body {
            background: #ffffff;
            border: 1px solid #e2e4e7;
            margin-bottom: 1rem;
        }
    }

    .components-base-control__help {
        margin-top: .5rem;
    }

    .components-panel__row {
        > div {
            flex-grow: 1;
            margin-right: 1rem;

            &:last-of-type {
                margin-right: 0;
            }
        }
    }

    .gf-block__notices {
        .components-snackbar {
            bottom: .5rem;
            position: fixed;
        }
    }
}
